@import url(https://fonts.googleapis.com/css2?family=Nova+Mono&family=PT+Sans&display=swap);
nav a {
  color: var(--quat) !important;
  font-size: 36px !important;
  font-family: "Nova Mono", monospace;
}

.nav-link.active {
  color: var(--sec) !important;
}

.navbar-toggler {
  background-color: var(--prim) !important;
}

.about-container {
  font-size: 24px !important;
}

#avatar {
  max-height: 15rem;
  border-radius: 50%;
}

.wrapper {
  padding-top: 50px;
  justify-content: space-around;
  height: 100%;
  display: flex;
  overflow: auto;
  flex-flow: row wrap;
  padding: 10px;
  align-content: flex-start;
}

.card {
  background-color: var(--prim);
  border-radius: 15px !important;
  height: 400px;
  margin: 2rem;
  position: relative;
  width: 400px;
}

.card .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background-color: var(--prim);
  border-radius: 15px 15px 0px 0px;
}

.card .img-container img {
  width: 100%;
  padding: 25px;
  margin-bottom: 20px;
  background: linear-gradient(#161616, var(--prim));
}

.card .card-body {
  font-family: "PT Sans", sans-serif !important;
  font-size: 18px;
  background-color: var(--prim);
  color: var(--sec);
  font-weight: bold;
  text-align: center;
  border-radius: 0px 0px 15px 15px;
}

.icon-img {
  padding: 1rem;
}

form {
  padding-top: 2rem;
}

.infoURL {
  font-size: 18px;
}

.info {
  color: var(--sec);
}

.form-dv {
  font-size: 24px;
}

.contact-submit button {
  background-color: var(--prim) !important;
}

.contact-submit button:hover {
  background-color: var(--tert) !important;
}

.resume-container {
  padding-top: 5rem;
  font-size: 24px;
}

.resume-download {
  padding: 1rem;
}

.resume-download button {
  background-color: var(--prim) !important;
}

.resume-download button:hover {
  background-color: var(--tert) !important;
}

:root {
  --prim: #3a91a1;
  --sec: #ff7c24ea;
  --tert: #0cecdd;
  --quat: #ebe4e4;
}

body {
  min-height: 100vh;
  min-width: 100vw;
  background-image: url("https://images.unsplash.com/photo-1602872250608-a17f7ba8933d?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=3334&q=80");
  background-size: cover;
  padding: 0 2rem 0 2rem;
  color: #ebe4e4 !important;
  color: var(--quat) !important;
}

main {
  min-height: 100vh;
}

a {
  text-decoration: none !important;
  color: #ebe4e4 !important;
  color: var(--quat) !important;
}

a:hover,
button:hover {
  color: #ff7c24ea !important;
  color: var(--sec) !important;
}

section {
  font-family: "PT Sans", sans-serif !important;
}

@media (min-width: 600px) {
  body {
    color: #ff7c24ea;
    color: var(--sec);
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer img {
  padding: 1rem;
}

